














































































import { Vue, Component } from 'vue-property-decorator';

import { ServiceRegex, ServiceDatetime, ServiceServerConfig, ServiceBase64, ServiceTypeof } from '@/../helpers';

import Card from '@/../components/card/card.vue';
import { FakeData } from '@/helpers/fake-data';

// form
interface IFormItem {
    isShowCard: boolean;
    showHeader: boolean;
}

interface IFormData {
    objectId: string;
    name: string;
}

interface ISelectOptionItem {}

interface IFormDataDisabled {}

interface IFormRelationSelectConfigs {}

interface IFormButtonDisabled {}

@Component({
    components: { Card },
})
export default class VuePageClass extends Vue {
    private imageBase64: string = FakeData.MALE_IMG;

    private selectOptionItem: ISelectOptionItem = {};

    private formItem: IFormItem = { isShowCard: false, showHeader: true };

    private formDataOriginal: IFormData = {
        objectId: '',
        name: '',
    };
    private formData: IFormData = { ...this.formDataOriginal };

    private formDataDisabledOriginal: IFormDataDisabled = {};
    private formDataDisabled: IFormDataDisabled = { ...this.formDataDisabledOriginal };

    private formConfigOriginal: IFormRelationSelectConfigs = {};
    private formConfig: IFormRelationSelectConfigs = { ...this.formConfigOriginal };

    private formButtonDisabled: IFormButtonDisabled = {};

    private i18n: any = this.$i18n;

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////
    private initFormData(): void {}

    private initFormConfig(): void {}

    //////////////////// Init Select Option  ////////////////////
    private async initSelectOption(): Promise<void> {}

    private async initSelectOptionXXX(): Promise<void> {}

    //////////////////// Form Show ////////////////////
    private goToFormPage(): void {
        this.$router.push('/component/form');
    }

    //////////////////// Form Set Data ////////////////////

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private formConfigValues(value: any, disabledBtuuon: boolean, id: string) {
        Object.keys(this.formData).forEach((key) => {
            if (id === key) this.formData[id] = value;
        });

        Object.keys(this.formButtonDisabled).forEach((key) => {
            if (id === key) this.formButtonDisabled[id] = disabledBtuuon;
        });
    }

    private formDisabledValues(value: boolean, id: string): void {
        Object.keys(this.formDataDisabled).forEach((key) => {
            if (id === key) this.formDataDisabled[id] = value;
        });
    }
    private formUploadImage(src: string): void {}

    private formDataSet(data: any) {
        this.formData.objectId = data.objectId;
        this.formData.name = data.name;
    }

    //////////////////// Modify API (save, delete, test)  ////////////////////

    private async tableDelete(data: any = undefined): Promise<void> {}

    private async dataSave(): Promise<void> {}

    //////////////////// Resolve text ////////////////////
    private resolvePhotoIsUrlOrBase64(value: string): string {
        if (!value) return undefined;

        if (ServiceBase64.isBase64Image(value)) return value;

        const imageURL: string = ServiceServerConfig.getURL + value;
        return ServiceRegex.isUrl(imageURL) ? imageURL : '';
    }

    private resolveDate(dateData: string | Date = undefined, dateFormat: string = 'YYYY/MM/DD HH:mm:ss'): string {
        try {
            let result: string = '';
            const date: Date = dateData instanceof Date ? dateData : new Date(dateData);

            if (ServiceTypeof.isDate(date)) {
                result = `${ServiceDatetime.datetime2String(date, dateFormat)}`;
            }

            return result;
        } catch (error) {
            throw 'resolveDate Error' + error;
        }
    }

    //////////////////// Modal ////////////////////
}
